@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700;800&display=swap');

:root {
	--font-header: 'Inter', sans-serif;
	--font-body: 'Inter', sans-serif;

	--transition-default: all 300ms ease-in-out;

	--color-header: #3c3c43;
	--color-body: #3C3C43;

	--color-green: #10a37f;
	--color-blue: #0071bc;

	--color-primary: #fff;
	--color-secondary: #f1f1f2;
	--color-tertiary: #dfdfe6;
}

*::-webkit-scrollbar {
	width: 0 !important;
}
*::-webkit-scrollbar-track {
	background-color: transparent !important;
}
*::-webkit-scrollbar-thumb {
	background-color: transparent !important;
}

html {
	overflow-x: hidden;
	overflow-y: scroll;
	scroll-behavior: smooth;
}

*,
*::after,
*:not(i)::before,
*::placeholder {
	font-family: var(--font-rajdhani);
	color: var(--color-cyan);
}

body {
	position: relative;
	z-index: 10;
	min-height: 100vh;
	line-height: 1.75 !important;
	letter-spacing: 0.02rem !important;
	font-family: var(--font-body) !important;
	background-color: var(--color-primary) !important;
	color: var(--color-body) !important;
}

body::before {
	content: '';
	width: 100%;
	height: 100%;
	top: 0;
	z-index: -3;
	position: absolute;
	background-image: url('grid.jpg');
	background-size: cover;
	background-position: center center;
	opacity: 0.05;
}

body::after {
	content: '';
	position: absolute;
	top: 0;
	z-index: -2;
	height: 100%;
	width: 100%;
	background-image: radial-gradient(at 27% 37%, hsla(215, 98%, 61%, 1) 0px, transparent 0%),
		radial-gradient(at 97% 21%, hsla(125, 98%, 72%, 1) 0px, transparent 50%),
		radial-gradient(at 52% 99%, hsla(354, 98%, 61%, 1) 0px, transparent 50%),
		radial-gradient(at 10% 29%, hsla(256, 96%, 67%, 1) 0px, transparent 50%),
		radial-gradient(at 97% 96%, hsla(38, 60%, 74%, 1) 0px, transparent 50%),
		radial-gradient(at 33% 50%, hsla(222, 67%, 73%, 1) 0px, transparent 50%),
		radial-gradient(at 79% 53%, hsla(343, 68%, 79%, 1) 0px, transparent 50%);
	filter: blur(100px) saturate(100%);
	opacity: 0.1;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	letter-spacing: -0.04375rem !important;
	font-weight: 600 !important;
	font-family: var(--font-header) !important;
	color: var(--color-header);
}

.blue-bg-color {
	background-color: var(--color-blue) !important;
}
.blue-color {
	color: var(--color-blue) !important;
}

.green-bg-color {
	background-color: var(--color-green) !important;
}
.green-color {
	color: var(--color-green) !important;
}

.primary-bg-color {
	background-color: var(--color-primary) !important;
}
.secondary-bg-color {
	background-color: var(--color-secondary) !important;
}
.tertiary-bg-color {
	background-color: var(--color-tertiary) !important;
}

.orange-gradient {
	background-image: linear-gradient(to right, #f59e0b, #ea580c, #eab308);
	-webkit-background-clip: text;
	background-clip: text;
	color: transparent;
}

.title {
	font-size: calc(35px + 1.5vw);
	text-align: center;
}

.output-field {
	line-height: normal !important;
	border: 2px solid var(--color-tertiary);
}

input:focus, select:focus {
	box-shadow: none !important;
}

input, select {
	color: var(--color-body) !important;
}